*{
	margin:0;
	padding:0;
}
body {
	padding:0;
	margin:0;
	font-size:"16px";
	font-family:"Microsoft YaHei";
	background:#fff;

}
$porpotion : 600/780;
$por : 750/950;
@font-face{
	font-family:Didott;
	src: url('./font-size/Dodit/didotitalic.eot');
}
.display-inline-block{
	display:inline-block;
}
.float-left{
	display: inline-block;
	float: left;
}
.float-right {
	display: inline-block;
	float: right;
}
.clear{
	clear:both;
}
@mixin bg-contain {
	background-repeat: no-repeat;
	background-size: contain;
}
.answer {
	font-family: "microsoft yahei";
	font-size: 14px;
	line-height: 24px;
}
.ask {
	font-family: "microsoft yahei";
	font-size: 14px;
	line-height: 24px;
}
.main{
	//width:1980px;
	width:100%;
	min-width:1680px;
	text-align:center;
	.ask{
		font-weight: bold;
		text-align: left;
		margin:5px;
	}
	.answer{
		text-align: left;
		margin:5px;
	}
	.banner{
		background-image: url(../images/bg_header.png);
		height: 876px;
		margin-bottom:60px;
		background-size: cover;
			background-repeat: no-repeat;
		.banner1 {

		}
	}
	.leader-word {
		border:2px solid #eee;
		width: 968px;
		height: 130px;
		text-align: center;
		margin: auto;
		margin-bottom: 74px;
		&::before{
			content: " â€œ ";
			font-family: "Arial";
			font-size: 184px;
			position: relative;
			top:-60px;
			left: -40%;
			background:linear-gradient(white,rgba(255, 255, 255, 0));
    		color: #d2d2d2;
		}
		p{
			margin: 10px 20px;
			position:relative;
			z-index: 9;
			top:-180px;
			font-size: 14px;
			line-height: 26px;
			font-family:"Microsoft YaHei";
		}

	}
	.part1{
		width: 1024px;
		margin:auto;
		margin-bottom: 100px;
		height: 920px;
		.part1-left {
			background-image:url(../images/bg_part1.png);
			background-repeat: no-repeat;
			background-size: contain;
			height: 880px;
			width: 688px;
			display: inline-block;
			float: left;
		}
		.part1-right {
			display: inline-block;
			height: 728px;
			width: 300px;
			//overflow: scroll;
			margin-left: 10px;
		}
		.line {
			width: 100%;
			height: 178px;
			background-image: url(../images/title_part1.png);
			background-size: contain;
			background-repeat: no-repeat;
		}
		
	}
	.trans1 {
		width: 100%;
		height: 500px;
		background-color: #efe5df;
		overflow: visible;
		margin-bottom: 120px;
		.sunwe {
			background-image: url(../images/pic_sunwe.png);
			background-size:contain;
			background-repeat: no-repeat;
			height: 736px;
			width: 962px;
			position:relative;
			top: -220px;
			margin:auto;
			margin-bottom: 120px;
		}
	}
	.part2 {
		width: 1024px;
		margin:auto;
		padding:0 6px;
		margin-bottom: 100px;
		height: 920px;
		.part2-left {
			height:500px;
			width: 390px;
			display: inline-block;
			float: left;
			.title_part2 {
				background-image: url(../images/title_part2.png);
				background-size: contain;
				background-repeat: no-repeat;
				width: 100%;
				height: 170px;
				margin-bottom: 24px;
			}
			.bg_part2_1 {
				background-image:url(../images/bg_part2_1.png);
				background-repeat: no-repeat;
				background-size: contain;
				width: 396px;
				height: 650px;
				
			}
		}
		.part2-right {
			height:  860px;
			width: 560px;
			display: inline-block;
			float: right;
			.bg_part2_2 {
				background-image:url(../images/bg_part2_2.png);
				background-repeat: no-repeat;
				background-size: contain;
				width: 250px;
				height: 412px;
				margin-right: 10px;
				margin-bottom: 34px;
			}
			.ask-and-answer {
				width: 290px;
			}
			.ask-and-answer_2{
				margin-top: 10px;
				clear: both;
			}
		}

	}
	.trans2 {
		width: 100%;
		height: 620px;
		background-image: url(../images/bg_trans2.png);
		background-repeat: no-repeat;
		background-size: contain;
		margin-bottom: 110px;
	}
	.part3 {
		margin:0 auto;
		width: 1024px;
		height: 1140px;
		.title_part3 {
			@include bg-contain;
			height: 140px;
			width: 990px;
			margin:0 auto;
			background-image: url(../images/title_part3.png);
		}
		.part3-left {
			width: 300px;
			margin:20px;
			display: inline-block;
			float: left;
			p {
				margin-top: 24px;
			}
		}
		.part3-right {
			width: 680px;
			height: 980px;
			display: inline-block;
			float: right;
			background-image: url(../images/bg_part3_1.png);
			@include bg-contain;
		}
		.part3-bottom {
			clear: both;
			position:relative;
			top:-460px;
			left: 56px;
			width: 490px;
			height: 700px;
			@include bg-contain;
			background-image: url(../images/bg_part3_2.png);
		}
	}
	.part4 {
		width: 100%;
		height: 1098px;
		margin-bottom: 100px;
		@include bg-contain;
		//background-size:cover;
		background-image :url(../images/bg_part4_1.png);
		.part4-contain {
			width: 1024px;
			height: 1098px;
			margin: auto;
			.part4-left {
				margin-top: 176px;
				width: 490px;
				height: 784px;
				display: inline-block;
				float: left;
				@include bg-contain;
				background-image :url(../images/bg_part4_2.png);
			}
			.part4-right {
				margin-top: 120px;
				//margin-left: 30px;
				width: 530px;
				height: 840px;
				display: inline-block;
				float: right;
				.title_part4 {
					@include bg-contain;
					background-image: url(../images/title_part4.png);
					width: 450px;
					height: 140px;

				}
				p {
					margin-top: 10px;
					line-height: 24px;
				}
				.bg_part4_3 {
					width: 100%;
					height: 280px;
					@include bg-contain;
					background-image: url(../images/bg_part4_3.png);
				}
			}
		}
	}
	.part5 {
		margin:0 auto 110px;
		width: 1060px;
		height: 886px;
		.part5-left {
			width: 410px;
			height: 750px;
			display: inline-block;
			float: left;
			.title_part5 {
				margin: 40px auto 130px;
				width: 338px;
				height: 168px;
				@include bg-contain;
				background-image:url(../images/title_part5.png);
			}
		}
		.part5-right {
			width: 630px;
			height: 784px;
			display: inline-block;
			float: right;
			@include bg-contain;
			background-image:url(../images/bg_part5.png);
		}
	}
	.part6 {
		width: 1024px;
		height: 1036px;
		margin:auto auto 100px;
		.title_part6 {
			@include bg-contain;
			background-image: url(../images/title_part6.png);
			width: 336px;
			height: 168px;
			margin: auto;
		}
		.pic-groups {
			width: 100%;
			height: 850px;
			margin:auto;
			position:relative;
			img {
				position: absolute;
				margin:auto;
				left: 20%;
				transition: width 2s height 2s z-index 2s left 2s right 2s;
			}
			.show-center {
				width: 540px;
				height: 840px;
				z-index: 99;
			}
			.show-left {
				left: -7%;
				top:50px;
				width: 460px;
				height: 710px;
			}
			.show-right {
				right: -40%;
				top:50px;
				width: 460px;
				height: 710px;
			}
			.unshow{
				display: none;
			}
			.icon_arrowleft {
				@include bg-contain;
				background-image:url(../images/icon_arrowleft.png);
				z-index: 100;
				width: 54px;
				height: 82px;
				position: absolute;
				top: 39%;
    			left: -2%;
			}
			.icon_arrowright {
				@include bg-contain;
				background-image:url(../images/icon_arrowright.png);
				z-index: 100;
				width: 54px;
				height: 82px;
				position: absolute;
				right: 2%;
    			top: 39%;
			}
		}
	}
}
.footer {
	width: 100%;
	min-width:1680px;
	height: 416px;
	.team {
		width: 100%;
		height: 228px;
		background: #000000;
		.team-info {
			margin: auto;
			width: 1024px;
			height: 100%;
			color: #fff;
			text-align: center;
			//padding-top: 50px;
			.ourteam{
				margin-top: 50px;
				width: 150px;
				display: inline-block;
				position: relative;
    			top: 24px;
				h1{
					margin-top: 16px;
				}
			}
			.member {
				width: 560px;
				margin: 70px auto;
				display: inline-block;
				span {
					display: inline-block;
					width: 130px;
					text-align: left;
				}
			}
			.qrcode {
				width: 180px;
				height: 180px;
				display: inline-block;
				float: right;
				@include bg-contain;
				background-image: url(../images/icon_qrcode.png);
				position:relative;
				top:28px;
			}
			
		}
	}
	.copyright {
		text-align: center;
		margin-top: 50px;
		.without-line{
			&::after{
				content: "";
			}
		}
		a {
			text-decoration: none;
			color: #333333;
			&::after {
				content: "|";
				color: #333333;
			}
		}
		p {
			font-size: 12px;
			line-height: 22px;
		}
	}
}